/* eslint-disable no-tabs */
import React, { Component, Fragment } from 'react'
import { Route } from 'react-router-dom'
import { v4 as uuid } from 'uuid'

import AuthenticatedRoute from './components/AuthenticatedRoute/AuthenticatedRoute'
import AutoDismissAlert from './components/AutoDismissAlert/AutoDismissAlert'
import Header from './components/Header/Header'
import SignUp from './components/auth/SignUp'
import SignIn from './components/auth/SignIn'
import SignOut from './components/auth/SignOut'
import ChangePassword from './components/auth/ChangePassword'
import CreateLocation from '../src/components/location/CreateLocation'
import IndexLocations from './components/location/IndexLocations'
import ShowLocation from './components/location/ShowLocation'
import UpdateLocation from './components/location/UpdatePopup'
// import Map from './components/map/Map'

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      user: null,
      msgAlerts: []
    }
  }

	setUser = (user) => this.setState({ user })

	clearUser = () => this.setState({ user: null })

deleteAlert = (id) => {
  this.setState((state) => {
    return { msgAlerts: state.msgAlerts.filter((msg) => msg.id !== id) }
  })
}

msgAlert = ({ heading, message, variant }) => {
  const id = uuid()
  this.setState((state) => {
    return {
      msgAlerts: [...state.msgAlerts, { heading, message, variant, id }]
    }
  })
}

render () {
  const { msgAlerts, user } = this.state

  return (
    <Fragment>
      <Header user={user} />
      {msgAlerts.map((msgAlert) => (
        <AutoDismissAlert
          key={msgAlert.id}
          heading={msgAlert.heading}
          variant={msgAlert.variant}
          message={msgAlert.message}
          id={msgAlert.id}
          deleteAlert={this.deleteAlert}
        />
      ))}
      <main className='container'>
        <Route
          path='/sign-up'
          render={() => (
            <SignUp msgAlert={this.msgAlert} setUser={this.setUser} />
          )}
        />
        <Route
          path='/sign-in'
          render={() => (
            <SignIn msgAlert={this.msgAlert} setUser={this.setUser} />
          )}
        />
        {/* <Route path='/' render={() => <Map />}></Route> */}
        <AuthenticatedRoute
          user={user}
          path='/sign-out'
          render={() => (
            <SignOut
              msgAlert={this.msgAlert}
              clearUser={this.clearUser}
              user={user}
            />
          )}
        />
        <AuthenticatedRoute
          user={user}
          path='/change-password'
          render={() => (
            <ChangePassword msgAlert={this.msgAlert} user={user} />
          )}
        />
        <AuthenticatedRoute
          msgAlert={this.msgAlert}
          user={user}
          path='/create-location'
          render={() => (
            <CreateLocation msgAlert={this.msgAlert} user={user} />
          )}
        />
        <AuthenticatedRoute
          msgAlert={this.msgAlert}
          user={user}
          exact
          path='/locations'
          render={() => (
            <IndexLocations msgAlert={this.msgAlert} user={user} />
          )}
        />
        <AuthenticatedRoute
          msgAlert={this.msgAlert}
          user={user}
          exact
          path='/locations/:id'
          render={() => <ShowLocation msgAlert={this.msgAlert} user={user} />}
        />
        <AuthenticatedRoute
          msgAlert={this.msgAlert}
          user={user}
          path='/locations/:id/edit'
          render={() => (
            <UpdateLocation msgAlert={this.msgAlert} user={user} />
          )}
        />
      </main>
    </Fragment>
  )
}
}

export default App
